/**
 * Mock Video Service Implementation
 * 
 * This module provides mock implementations of the Cloudflare Stream video API
 * to allow the frontend to function without the backend API being fully implemented.
 * 
 * All methods return hardcoded responses instead of making actual API calls.
 */

// Type definitions for video data and API responses
export interface VideoData {
  uid: string;
  thumbnail: string;
  preview: string;
  readyToStream: boolean;
  readyToStreamAt: string;
  status: {
    state: string;
    pctComplete: string;
    errorReasonCode: string;
    errorReasonText: string;
  };
  meta: {
    filename: string;
    filetype: string;
    name: string;
    relativePath: string;
    type: string;
  };
  duration: number;
  created: string;
  modified: string;
  size: number;
  input: {
    width: number;
    height: number;
  };
  playback: {
    hls: string;
    dash: string;
  };
}

export interface VideoApiResponse {
  success: boolean;
  status: number;
  message: string;
  data: {
    result: VideoData[];
  };
}

export interface UploadUrlResponse {
  uploadURL: string;
  uid: string;
}

export interface VideoStatusResponse {
  success: boolean;
  readyToStream: boolean;
  status: string;
  video: {
    uid: string;
    readyToStream: boolean;
    thumbnail: string;
    playback: {
      hls: string;
      dash: string;
    };
    created: string;
    duration: number;
    status: {
      state: string;
    };
  };
}

// Sample mock video data
const sampleVideo: VideoData = {
  uid: "mock-video-uid-123456",
  thumbnail: "https://imagedelivery.net/example/thumbnail.jpg",
  preview: "https://imagedelivery.net/example/preview.jpg",
  readyToStream: true,
  readyToStreamAt: new Date().toISOString(),
  status: {
    state: "ready",
    pctComplete: "100",
    errorReasonCode: "",
    errorReasonText: ""
  },
  meta: {
    filename: "sample-video.mp4",
    filetype: "video/mp4",
    name: "Sample Video",
    relativePath: "",
    type: "video/mp4"
  },
  duration: 60.5,
  created: new Date().toISOString(),
  modified: new Date().toISOString(),
  size: 5242880, // 5MB
  input: {
    width: 1920,
    height: 1080
  },
  playback: {
    hls: "https://videodelivery.net/mock-video-uid-123456/manifest/video.m3u8",
    dash: "https://videodelivery.net/mock-video-uid-123456/manifest/video.mpd"
  }
};

// Mock implementation of the video service
const videoService = {
  /**
   * Test Cloudflare connection and get a sample video
   */
  testCloudflareConnection: () => {
    console.log('MOCK: Testing Cloudflare connection');
    return Promise.resolve({
      success: true,
      status: 200,
      message: "Mock connection successful",
      data: {
        result: [sampleVideo]
      }
    });
  },

  /**
   * Get a list of all videos
   */
  getAllVideos: () => {
    console.log('MOCK: Getting all videos');
    return Promise.resolve({
      success: true,
      status: 200,
      message: "Videos retrieved successfully",
      data: {
        result: [sampleVideo]
      }
    });
  },

  /**
   * Get a single video by its UID
   */
  getVideoByUid: (uid: string) => {
    console.log(`MOCK: Getting video with UID ${uid}`);
    return Promise.resolve({
      success: true,
      status: 200,
      message: "Video retrieved successfully",
      data: {
        result: [{
          ...sampleVideo,
          uid: uid // Use the requested UID
        }]
      }
    });
  },

  /**
   * Get a one-time upload URL for direct creator uploads
   */
  getUploadUrl: (maxDurationSeconds: number = 3600) => {
    console.log(`MOCK: Getting upload URL with maxDurationSeconds=${maxDurationSeconds}`);
    
    // Generate a mock UID
    const mockUid = `mock-upload-${Date.now()}-${Math.floor(Math.random() * 1000)}`;
    
    return Promise.resolve({
      uploadURL: "https://example.com/mock-upload",
      uid: mockUid
    });
  },

  /**
   * Upload a video file to Cloudflare Stream using a one-time upload URL
   */
  uploadVideoFile: (
    uploadURL: string, 
    file: File, 
    onUploadProgress?: (progress: number) => void
  ) => {
    console.log(`MOCK: Uploading file ${file.name} to ${uploadURL}`);
    
    // Simulate upload progress
    if (onUploadProgress) {
      const simulateProgress = () => {
        let progress = 0;
        const interval = setInterval(() => {
          progress += 10;
          onUploadProgress(progress);
          if (progress >= 100) {
            clearInterval(interval);
          }
        }, 300);
      };
      
      simulateProgress();
    }
    
    return Promise.resolve();
  },

  /**
   * Check the status of an uploaded video
   */
  checkVideoStatus: (videoId: string) => {
    console.log(`MOCK: Checking status for video ${videoId}`);
    
    return Promise.resolve({
      success: true,
      readyToStream: true,
      status: "ready",
      video: {
        uid: videoId,
        readyToStream: true,
        thumbnail: "https://imagedelivery.net/example/thumbnail.jpg",
        playback: {
          hls: `https://videodelivery.net/${videoId}/manifest/video.m3u8`,
          dash: `https://videodelivery.net/${videoId}/manifest/video.mpd`
        },
        created: new Date().toISOString(),
        duration: 60.5,
        status: {
          state: "ready"
        }
      }
    });
  }
};

export default videoService; 